$prefix: app-bar;

.#{$prefix} {
  display: flex;
  align-items: center;

  padding: 0 16px;
  height: 56px;

  &--active {
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .2), 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12);
    transition: box-shadow .2s linear, -webkit-box-shadow .2s linear;
  }
  
  &__title {
    align-self:flex-end;

    margin-left: 20px;
    margin-bottom: 6px;
  }
  &__right {
    margin-left: auto;
  }
}
